import { getMessageById } from '@/lib/data';
import { notFound } from 'next/navigation';
import { Header } from '@/components/Header';
import { Button } from '@/components/ui/button';
import Link from 'next/link';
import { ChevronLeft } from 'lucide-react';
import { format } from 'date-fns';
import { Card, CardContent, CardFooter } from '@/components/ui/card';
import { MessageSquare } from 'lucide-react';

type PageProps = {
  params: {
    id: string;
  };
};

export default function MessagePage({ params }: PageProps) {
  const message = getMessageById(params.id);

  if (!message) {
    notFound();
  }

  const formattedTimestamp = format(
    new Date(message.timestamp),
    "MMMM d, yyyy 'at' h:mm a"
  );

  return (
    <div className="flex min-h-dvh flex-col">
      <Header />
      <main className="flex-1">
        <div className="container mx-auto max-w-2xl px-4 py-8 md:py-16">
          <div className="mb-4">
            <Button asChild variant="link" className="pl-0 text-muted-foreground">
              <Link href={`/bottle/${message.recipient}`}>
                <ChevronLeft className="mr-1 h-4 w-4" />
                Back to {message.recipient}'s bottle
              </Link>
            </Button>
          </div>

          <Card>
            <CardContent className="relative p-6">
              <MessageSquare className="absolute -top-3 -left-3 h-8 w-8 text-muted" />
              <blockquote className="border-l-2 border-border pl-4 italic text-muted-foreground">
                {message.content}
              </blockquote>
            </CardContent>
            <CardFooter className="p-6 pt-0 text-sm text-muted-foreground">
                <p>Received on {formattedTimestamp}</p>
            </CardFooter>
          </Card>
        </div>
      </main>
    </div>
  );
}

export function generateMetadata({ params }: PageProps) {
    const message = getMessageById(params.id);
    return {
      title: message ? 'Message' : 'Message Not Found',
    };
  }
